.product_card{
    max-width:220px;
    overflow:hidden;
    height:350px;
    padding:15px;
    box-shadow: 0 0 15px #ddd;
    margin:10px 0;
    position: relative;
 
}
.product_card img{
    width: 100%;
    height: 180px;
    display: block;
    object-fit: cover;
}
.product_box h3{
    width: 100%;
    text-overflow:ellipsis;
    overflow: hidden;
    white-space: nowrap;
    text-transform: capitalize;
    cursor: pointer;
    color:#323232
}
.product_card span{
    color: crimson;
}
.product_card p{
    width: 100%;
    display: -webkit-box;
    height: 40px;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
    color:#323232
}
.row_btn{
    width: 100%;
    margin-top: 4px;
    display: flex;
    justify-content: space-between; 
}
.row_btn a{
    width:50%;
    text-align: center;
    text-transform: uppercase;
    color: white;
    font-weight: 600;
    letter-spacing: 2px;
    padding:4px;
}
#btn_buy{
    background: #555;
    margin-right: 5px;

}
#btn_view{
    background: rgb(17, 76, 240);
    margin-right: 5px;

}
.product_card input{
    position: absolute;
    width: 25px;
    height: 25px;
}